function _avg(numbers) {
    return numbers.reduce(function(sum, num){
        return sum + num;
    }, 0) / numbers.length;
}

function _var_sp(numbers, divisor, avg) {
    if (avg == null) {
        avg = _avg(numbers);
    }
    return numbers.reduce(function(sum, num){
        return sum + Math.pow(num - avg, 2);
    }, 0) / divisor;
}

function _stdev_sp(numbers, divisor) {
    return Math.sqrt(_var_sp(numbers, divisor));
}

